.logincontainer{
    width: 100%;
}

.login{
    display: flex;
    width: 100%;
    justify-content: space-between;
}

#logo{
    width: 230px;
    height: 187px;
    display: flex;
    align-items: center;
}


.loginDetails{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#welcometxt{
    color: #000;
    font-size: 20px;
    margin-bottom: 20px;
}

#emailpass{
    color: gray;
    margin-bottom: 20px;
}

#titles{
   display: flex;
   justify-content: flex-start;
    
}

.input{
    display: flex;
    flex-direction: column;
    flex: 1;
}

.btn{
    margin-top: 40px;

}

.ml-2{
    width: 500px;
    display: flex;
    justify-content: center;
    align-items: center;

    
}

.space{
    margin-top:30px;
}

#remember{
    margin-bottom: 20px;
    margin-top: 20px;
}

@media all and (max-width:1013px) {
    #coverimg{
        display: none;
    }
    .ml-2{
        width: 300px;
    }
    .btn{
        display: flex;
        align-self: center;
    }

}